import "./projects.scss";
import SocialNet from "../../images/social-net.png";
import productsShop from "../../images/cosmetic-web.png";
import portfolio from "../../images/portfolio.png";
import talWeb from "../../images/talWeb.png";

const Projects = () => {
  const projectLinks = {
    socialNet: "https://phpstack-737101-2470087.cloudwaysapps.com",
    productsShop: "https://products-shopp.netlify.app",
    portfolio: "https://roi-lopez-portfolio.netlify.app",
    talWeb: "https://tal-lopez.co.il",
  };

  function toProject(project) {
    if (project === "socialNet") {
      return window.open(`${projectLinks.socialNet}`, "_blank");
    }

    if (project === "productsShop") {
      return window.open(`${projectLinks.productsShop}`, "_blank");
    }

    if (project === "portfolio") {
      return window.open(`${projectLinks.portfolio}`, "_blank");
    }

    if (project === "talWeb") {
      return window.open(`${projectLinks.talWeb}`, "_blank");
    }
  }
  return (
    <div className="container container-project  d-flex justify-content-center d-flex flex-column justify-content-center align-items-center ">
      <div id="projects"></div>

      <div className="row d-flex justify-content-center">
        <div className="col-12 text-center mb-5 pb-2">
          <h1>PROJECTS</h1>
          <div className="bottom-line"></div>
          <span>Here Are Some of My Projects</span>
        </div>

        {/* SocialNet */}
        <div className="col-12 col-xl-5 col-lg-6  card containerProjectImage">
          <div className="pt-2" onClick={() => toProject("socialNet")}>
            <img
              className="card-img-top "
              style={{ borderRadius: "10px" }}
              src={SocialNet}
              alt=""
            />
          </div>

          <div className="card-body">
            <div className="float-end ">
              <a
                style={{
                  textDecoration: "none",
                  color: "white",
                  fontSize: "20px",
                }}
                href="https://github.com/roilopezz/social-network-frontend"
                target="_blank"
              >
                <i className="bi bi-github text-danger"></i>
              </a>
            </div>
            <h5 className="card-title">Social Network</h5>
            <p className="card-text pb-3">
              <span>
                Login , Post , Like , Add Comment , Upload Photo , Delete
              </span>

              <div>
                <span className="infoProjectSkills">React</span>
                <span className="infoProjectSkills">Node.js</span>
                <span className="infoProjectSkills">bootstrap</span>
                <span className="infoProjectSkills">Axios</span>
                <span className="infoProjectSkills">Joi</span>
                <span className="infoProjectSkills">material-ui</span>
                <span className="infoProjectSkills">bcrypt</span>
                <span className="infoProjectSkills">express</span>
                <span className="infoProjectSkills">multer</span>
                <span className="infoProjectSkills">Socket.io</span>
                <span className="infoProjectSkills">MongoDB</span>
              </div>
            </p>
          </div>
        </div>

        {/* Product Shop */}
        <div className="col-12 col-xl-5 col-lg-6  card containerProjectImage ms-xl-2 ">
          {/* <img src="..." className="card-img-top" alt="..." /> */}
          <div className="pt-2" onClick={() => toProject("productsShop")}>
            <img
              className="card-img-top "
              style={{ borderRadius: "10px" }}
              src={productsShop}
              alt=""
            />
          </div>
          <div className="card-body ">
            <div className="float-end ">
              <a
                style={{
                  textDecoration: "none",
                  color: "white",
                  fontSize: "20px",
                }}
                href="https://github.com/roilopezz/Products-Shop"
                target="_blank"
              >
                <i className="bi bi-github text-danger"></i>
              </a>
            </div>
            <h5 className="card-title">Products Shop</h5>
            <p className="card-text pb-2">
              <span>Shop app , Add , Delete</span>
              <div>
                <span className="infoProjectSkills">React</span>
                <span className="infoProjectSkills">bootstrap</span>
              </div>
            </p>
          </div>
        </div>

        {/* Portfolio */}
        <div className="col-12 col-xl-5 col-lg-6  card containerProjectImage ">
          {/* <img src="..." className="card-img-top" alt="..." /> */}
          <div className="pt-2" onClick={() => toProject("portfolio")}>
            <img
              className="card-img-top "
              style={{ borderRadius: "10px" }}
              src={portfolio}
              alt=""
            />
          </div>
          <div className="card-body ">
            <div className="float-end ">
              <a
                style={{
                  textDecoration: "none",
                  color: "white",
                  fontSize: "20px",
                }}
                href="https://github.com/roilopezz/Products-Shop"
                target="_blank"
              ></a>
            </div>
            <h5 className="card-title">Portfolio</h5>
            <p className="card-text pb-2">
              <span>My Portfolio</span>
              <div>
                <span className="infoProjectSkills">React</span>
                <span className="infoProjectSkills">bootstrap</span>
              </div>
            </p>
          </div>
        </div>

        {/* CosmeticWeb */}
        <div className="col-12 col-xl-5 col-lg-6  card containerProjectImage ms-xl-2 ">
          {/* <img src="..." className="card-img-top" alt="..." /> */}
          <div className="pt-2" onClick={() => toProject("talWeb")}>
            <img
              className="card-img-top "
              style={{ borderRadius: "10px" }}
              src={talWeb}
              alt=""
            />
          </div>
          <div className="card-body ">
            <div className="float-end ">
              <a
                style={{
                  textDecoration: "none",
                  color: "white",
                  fontSize: "20px",
                }}
                href="https://github.com/roilopezz/Products-Shop"
                target="_blank"
              ></a>
            </div>
            <h5 className="card-title">Cosmetic Website</h5>
            <p className="card-text pb-2">
              <span>Landing Page + form</span>
              <div>
                <span className="infoProjectSkills">React</span>
                <span className="infoProjectSkills">Node.js</span>
                <span className="infoProjectSkills">Express</span>
                <span className="infoProjectSkills">Googleapis</span>
                <span className="infoProjectSkills">Nodemailer</span>
                <span className="infoProjectSkills">Axios</span>
                <span className="infoProjectSkills">Mui</span>
              </div>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Projects;
