import "./about.scss";

const About = () => {
  return (
    <div className="container container-about">
      <div id="about"></div>
      <div className="row">
        <div className="col-12 text-center  pb-5">
          <h1>ABOUT ME</h1>
          <div className="bottom-line"></div>
        </div>
        <div className="col-12 col-xl-5 ">
          I discovered the great passion for coding as a child, and didn't stop
          ever since! I planned, designed and developed major and minor web
          projects, using variety of programming languages. These days I'm
          working on new projects and learning new skills every day, while
          looking for my first professional experience.
        </div>
        <div className="col-0 col-xl-1"></div>
        <div className="col-12 col-xl-4 mt-4 mt-xl-0 containerSkills">
          <div>My Top Skills :</div>
          <div className="subContainerSkills">
            <span className="skills">JavaScript</span>
            <span className="skills">HTML</span>
            <span className="skills">CSS</span>
            <span className="skills">React.js</span>
            <span className="skills">Node.js</span>
            <span className="skills">MongoDB</span>
          </div>

          <div className="mt-4">Also Experienced In :</div>
          <div className="subContainerSkills">
            <span className="skills">Angular</span>
            <span className="skills">SASS</span>
            <span className="skills">CSS</span>
            <span className="skills">Express</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
